import uniIcons from "../../components/uni-ui/uni-icons/uni-icons.vue"
import uniSearchBar from "../../components/uni-ui/uni-search-bar/uni-search-bar.vue"
export default {
	components: {
		uniSearchBar,
		uniIcons
	},
	data() {
		return {
			historyData: [],
			hotKeywordList: null,
			results: [],
			value: '',
			openId: ''
		};
	},
	methods: {
		getSearchRelated() {
			this.$api.getSearchRelated(this.openId).then(res => {
				if (res.status === 200) {
					this.historyData = res.data.historyData
					this.hotKeywordList = res.data.hotKeywordList
				}
			})
		},
		searchVal(e) {
			if (e.value === '') {
				// this.results = []
			}
		},
		goSearch(e) {
			if (e.value) {
				this.value = e.value
			} else if (typeof e === 'string') {
				this.value = e
			} else {
				uni.showToast({
					icon: "none",
					title: "参数不全",
					duration: 800
				})
				return;
			}
			this.$api.getSearch(this.value).then(res => {
				if (res.status === 200) {
					this.results = res.data.keywords
				}
				this.getAddSearch(this.value)
			})
		},
		getAddSearch(value) {
			this.$api.getAddSearch({
				keyword:value,
				openId:this.openId
			})
		},
		clearHistory() {
			uni.showModal({
				title: '清除记录',
				content: '是否清除历史浏览记录',
				success: (res) => {
					if (res.confirm) {
						this.$api.getEmptySearch(this.openId).then(res => {
							if (res.status === 200) {
								this.historyData = []
								uni.showToast({
									title: res.data.data,
									duration: 800
								});
							}
						})
					} else if (res.cancel) {
						uni.showToast({
							icon: "none",
							title: "您已取消清除记录",
							duration: 800
						});
					}
				}
			});
		},
		cancel() {
			uni.navigateBack({
				delta: 1
			})
		},
		gotoDetail(id) {
			uni.navigateTo({
				url: `/pages/goodsDetail/goodsDetail?id=${id}`
			})
		}
	},
	watch: {
		results(val) {
			if (val.length === 0) {
				this.getSearchRelated()
			}
		}
	},
	onLoad() {
		this.openId = uni.getStorageSync('openId')
		this.getSearchRelated()
	}
}
